.about-home {
    border-top-left-radius: 65px;
    border-top-right-radius: 65px;
    @include adaptiveValue("padding-top", 90, 42, 1296, 576, 1);
    background-color: var(--white);
    position: relative;
    z-index: 5;
    &__top {
        display: flex;
        justify-content: space-between;
        padding: 0 60px;
        @media (max-width:$desktop) {
            padding: 0;
        }
        @media (max-width:$smallTablet-max){
             justify-content: center; 
        }
    }

    &__title {
        color: var(--black);
        font: var(--title-3);
        letter-spacing: -0.32px;
        margin-right: 32px;
        span {
            color: var(--blue);
        }
        @media (max-width:$smallTablet-max){
             margin-right: 0;
             text-align: center; 
        }
    }
    &__badge{
        @media (max-width:$smallTablet-max){
             display: none; 
        }
    }
    &__content {
        margin-top: 155px;
        border-radius: 74px;
        background-color: #f3f6ff;
        padding: 0 60px;

        @media (max-width:$desktop) {
            padding: 0;
            background-color: transparent;
            border-radius: 0;
            margin-top: 46px;
        }
    }

    &__items {
        transform: translateY(-55px);
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 24px;
        grid-row-gap: 24px;

        @media (max-width:$desktop) {
            grid-column-gap: 16px;
            grid-row-gap: 16px;
            grid-template-columns: 1fr;
            transform: translateY(0);
        }
    }

    &__item {
        @include adaptiveValue("border-radius", 54, 24, 1296, 576, 1);
        border: 1px solid #dce4fc;
        background-color: var(--white);

        display: flex;
        flex-direction: column;
        align-items: start;
        text-align: left;
        white-space: normal;
        padding: 24px 24px 42px 42px;

        &--1 {
            .about-home__item-icon {
                background: #ffdaf2;
            }
        }

        &--2 {
            .about-home__item-icon {
                background: #a1c5ff;
            }
        }
        &--1,&--2{
            .about-home__item-text{
                padding-bottom: 20px;
                left: -11px;
            }
        }
        &--3 {
            .about-home__item-icon {
                background: #b7b6ff;
            }
            .about-home__item-link{
                &._active{
                    .about-home__item-text{
                    transform: translateX(-50%) translateY(0);
                    }
                }
            }
            .about-home__item-text{
                padding-bottom: 20px;
            left: 50%;
            transform: translateX(-50%) translateY(10px);
            }
        }

        @media (max-width:$desktop) {
            padding: 20px 20px 30px 30px;
        }
    }

    &__item-icon {
        width: 73px;
        height: 73px;
        border-radius: 50%;

        display: flex;
        align-items: center;
        justify-content: center;

        align-self: end;

        svg {
            width: 30px;
            height: 30px;
            stroke: var(--black);
            fill: var(--white);
            stroke-width: 1.5px;
            @media (max-width:$desktop){
                width: 22px;
                height: 22px;
            }
        }

        @media (max-width:$desktop) {
            width: 52px;
            height: 52px;
        }
    }

    &__item-subtitle {
        margin-top: -44px;
        color: #141414;
        font: var(--text-1);
        font-weight: 500;
    }

    &__item-title {
        color: #141414;
        font: var(--title-1);
        letter-spacing: -1px;
        span{
            font: var(--text-1);
            font-weight: 600;
        }
        @media (max-width:$desktop){
            letter-spacing: -0.722px;
        }
    }

    &__item-descr {
        color: var(--black);
        font: var(--text-1);
        font-weight: 500;
        max-width: 244px;
        span{
            display: block;
        }
    }

    &__item-link {
        margin-top: 32px;
        @media (max-width:$desktop) {
            margin-top: 20px;
        }
    }
    &__item-target{
        color: var(--blue);
        font: var(--text-2-500);
    }
    &__item-text{
        >div{
            p{
                &:not(:last-child){
                    margin-bottom: 24px;
                    @media (max-width:$desktop){
                         margin-bottom: 16px; 
                    }
                }
            }
        }
    }
    @media (max-width:$desktop){
        border-top-left-radius: 42px;
        border-top-right-radius: 42px;
    }
}
