.app-performers-digitale-umgebung {

    background-color: var(--white);
    position: relative;
    z-index: 5;
    border-top-left-radius: 88px;
    border-top-right-radius: 88px;
    padding-top: 164px;

    &__container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 24px;
        grid-row-gap: 40px;
        @media (max-width:$desktop){
             grid-row-gap: 16px; 
        }
    }

    &__title {
        span {
            color: var(--blue);
        }
    }

    &__info {
        max-width: 435px;

        .app-performers-digitale-umgebung {
            &__title {
                span {
                    display: inline-block;
                }

                @media (max-width:$smallTablet-max) {
                    text-align: center;
                }
            }

            &__descr {
                margin-top: 24px;
                font: var(--text-1);

                @media (max-width:$desktop) {
                    margin-top: 12px;
                }

                @media (max-width:$smallTablet-max) {
                    text-align: center;
                    max-width: 270px;
                    margin: 12px auto 0;
                }
            }
        }

        @media (max-width:$smallTablet-max) {
            max-width: none;
            margin-bottom: 70px;
        }

        @media (max-width:$mobile-max) {
            margin-bottom: 60px;
        }
    }

    &__elements {
        margin-top: 32px;

        li {
            display: flex;

            &:not(:last-child) {
                margin-bottom: 12px;
            }

            div {
                margin-top: 3px;
                border-radius: 50%;
                background-color: var(--black);
                width: 25px;
                height: 25px;
                flex: 0 0 25px;
                display: flex;
                align-items: center;
                justify-content: center;

                margin-right: 20px;

                svg {
                    width: 14px;
                    height: 14px;
                    fill: none;
                    stroke-width: 1.5px;
                    stroke: var(--white);

                    @media (max-width:$desktop) {
                        width: 11px;
                        height: 11px;
                    }
                }

                @media (max-width:$desktop) {
                    width: 20px;
                    height: 20px;
                    flex: 0 0 20px;
                    margin-right: 16px;
                }
            }

            span {
                font: var(--text-1);
            }
        }

        @media (max-width:$desktop) {
            margin: 12px auto 0;
            grid-column: 1/-1;
        }

    }

    &__image {
        max-height: calc(100% - 57px);
        @include adaptiveValue("border-radius", 70, 42, 1296, 576, 1);
        background: linear-gradient(196deg, #6BA6FF -44.28%, #1541DF 107.8%);
        display: flex;
        justify-content: center;
        align-items: end;
        position: relative;

        picture {
            &:nth-child(1) {
                max-width: percent(434, 636);
                transform: translateY(8px) translateX(20px);

                @media (max-width:$desktop) {
                    transform: translateY(8px);
                    display: none;
                }

                @media (max-width:$mobile-max) {
                    max-width: 85%;
                }
            }

            &:nth-child(2) {
                position: absolute;
                top: 22.5%;
                left: 17%;

                height: percent(148, 508);

                transition: left $transitionSpeed $transitionFunction;

                img {
                    width: 100%;
                    height: 100%;
                }

                @media (max-width:$desktop) {
                    display: none;
                }
            }

            &:nth-child(3) {
                display: none;
                transform: translateY(8px);
                max-width: percent(434, 636);

                @media (max-width:$desktop) {
                    display: block;
                }

                @media (max-width:$mobile-max) {
                    max-width: 85%;
                }
            }
        }

        @media (min-width:$desktop-min) {
            &:hover {
                picture {
                    &:nth-child(2) {
                        left: calc(17% - 30px)
                    }
                }
            }
        }

        @media (max-width:$smallTablet-max) {
            max-width: 600px;
            margin: 0 auto;
        }

        @media (max-width:$mobile-max) {
            max-height: calc(100% - 40px - 8px);
        }
    }

    &__item {
        &:nth-child(1) {
            grid-column: 1/-1;

            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: 26px;
            margin-bottom: -57px;

            @media (max-width:$desktop) {
                grid-column-gap: 24px;
            }

            @media (max-width:$smallTablet-max) {
                grid-template-columns: 1fr;
            }

            @media (max-width:$mobile-max) {
                margin-bottom: -48px;
            }
        }

        &:nth-child(2),
        &:nth-child(3) {
            background-color: #FBFBFB;
            border: 1px solid #E7EAF2;
            border-radius: 40px;
            padding: 29px 46px;
            overflow: hidden;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center right;
            display: flex;
            align-items: start;
            min-height: 306px;

            .app-performers-digitale-umgebung__title {
                color: var(--dark);
                font: var(--subtitle-2);
            }

            @media (max-width:$desktop) {
                border-radius: 42px;
                grid-column: 1/-1;
                padding: 40px 36px;
                min-height: 294px;
                max-height: 294px;
            }
        }

        &:nth-child(2) {
            position: relative;
            overflow: visible;

            .app-performers-digitale-umgebung__badges {
                overflow: hidden;
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                height: calc(100% + 15px);
                @media (max-width:$desktop){
                     max-width: 300px;
                     height: 300px; 
                     left: calc(100% - 300px)
                }
            }

            .app-performers-digitale-umgebung__badge {
                position: absolute;
                transform: translateY(-100px);
                opacity: 0;

                &--1 {
                    top: -2.55%;
                    right: 13.2%;

                    transition: transform 0.25s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;

                    @media (max-width:$desktop) {
                        display: none;
                    }
                }

                &--2 {
                    top: 30.5%;
                    right: 21.6%;
                    transition: transform 0.3s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;

                    @media (max-width:$desktop) {
                        display: none;
                    }
                }

                &--3 {
                    top: 45%;
                    right: 23%;
                    transition: transform 0.35s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;

                    @media (max-width:$desktop) {
                        width: 113px;
                    }
                }

                &--4 {
                    top: 54.1%;
                    right: 1%;
                    transition: transform 0.4s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;
                        @media (max-width:$desktop){
                             width: 141px; 
                             top: 55.1%;
                             right: -4%;
                        }
                }

                &--5 {
                    top: 70%;
                    right: 28.5%;
                    transition: transform 0.45s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;
                        @media (max-width:$desktop){
                             width: 140px; 
                        }
                }

                &--6 {
                    top: 77%;
                    right: 42.6%;
                    transition: transform 0.5s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;
                        @media (max-width:$desktop){
                             width: 131px; 
                             top: 80%;
                             right: 42.6%;
                        }
                }

                &--7 {
                    top: 69.1%;
                    right: 1.7%;
                    transition: transform 0.55s $transitionFunction,
                        opacity $transitionSpeed $transitionFunction;
                        @media (max-width:$desktop){
                             width: 122px; 
                             top: 68.1%;
                             right: -9.3%;
                        }
                }

                @media (max-width:$desktop) {
                    transform: translateY(0);
                    opacity: 1;
                }
            }

            .app-performers-digitale-umgebung__title {
                max-width: 291px;
                margin-top: 17px;
                position: relative;
                z-index: 1;

                @media (max-width:$desktop) {
                    margin-top: 0;
                    max-width: 320px;
                }
            }

            &._animation-active {
                .app-performers-digitale-umgebung__badge {
                    transform: translateY(0);
                    opacity: 1;
                }
            }
        }

        &:nth-child(3) {
            .app-performers-digitale-umgebung__title {
                max-width: 236px;
                flex: 1 0 236px;
                margin-top: 17px;
                letter-spacing: -0.13px;

                @media (max-width:$desktop) {
                    margin-top: 0;
                    margin-bottom: 60px;
                 max-width: none;
                }
            }

            .app-performers-digitale-umgebung__card {
                background: #DBE2FF;
                border-radius: 36px;
                padding: 30px;
                flex: 0 0 340px;
                margin-left: 185px;
                transition: margin $transitionSpeed $transitionFunction;

                @media (max-width:$desktop) {
                    background: transparent;
                    border-radius: 0;
                    padding: 0;
                    flex: auto;
                    margin-left: 0;
                    transform: translateY(9px) translateX(78px);
                }
            }

            @media (min-width:$desktop-min) {
                &._animation-active {
                    .app-performers-digitale-umgebung__card {
                        margin-left: 73px;
                    }
                }
            }
            @media (max-width:$desktop){
                 display: block; 
            }
        }
    }

    @media (max-width:$desktop) {
        margin-top: 60px;
        padding: 0;
        border-radius: 0;
    }
}
