.for-whom-skud-two {
    @include adaptiveValue("margin-top", 120, 48, 1296, 576, 1);
    .container{
        @media (max-width:$desktop){
             padding: 0; 
        }
    }
    &__title {
        line-height: math.div(42, 38);
        margin: 0 auto;
        text-align: center;
        span {
            color: var(--blue);
        }
        @media (max-width:$desktop){
             padding: 0 15px; 
        }
    }

    &__text {
        max-width: 822px;
        margin-top: 22px;
        margin-left: auto;
        margin-right: auto;
        font: var(--text-1);
        text-align: center;
        @media (max-width:$desktop){
            margin-top: 16px; 
            padding: 0 15px;
        }
    }

    &__wrapper {
        margin-top: 68px;

        border: 1px solid #E7EAF2;
        border-radius: 70px;
        background: #FFF;

        padding: 91px 0 66px;
        @media (max-width:$desktop){
            margin-top: 32px;
            padding: 63px 0 47px;
            border-radius: 0;
            border-left: none;
            border-right: none;
        }
    }

    &__slider {
        padding: 0 32px;
            .swiper-slide {
                text-align: center;
                user-select: none;
                --width: 196px;
                overflow: hidden;
                width: auto;
                height: auto;
                display: flex;
                flex-direction: column;

                picture {
                    flex-grow: 1;
                    max-height: 196px;
                    @media (max-width:$smallTablet-max){
                         max-height: 155px; 
                    }
                    @media (max-width:$mobile-max){
                         max-height: 136px; 
                    }
                }

                img {
                    border-radius: 36px;
                    height: 100%;
                    width: var(--width);
                
                    @media (max-width:$desktop) {
                        border-radius: 24px;
                    }
                }

                span {
                    margin-top: 18px;
                    display: inline-block;
                    color: var(--dark);
                    font: var(--text-2-400);
                    max-width: var(--width);
                    transition: color $transitionSpeed $transitionFunction;

                    @media (max-width:$desktop) {
                        margin-top: 12px;
                    }
                    @media (max-width:$mobile-max){
                         font-size: 12px; 
                    }
                }

                &.focus-visible {
                    outline: none;

                    span {
                        color: var(--blue);
                    }
                }
                @media (max-width:$smallTablet-max){
                     --width: 155px;
                }
                @media (max-width:$mobile-max){
                     --width:136px; 
                }
            }
            @media (max-width:$desktop){
                padding: 0 24px;
            }
            @media (max-width:$smallTablet-max){
                 padding: 0 16px;
                 margin: 0 -16px; 
            }
    }
    &__nav{
        margin: 30px auto 0;
        padding: 0 16px;
        display: flex;
        justify-content: center;
        align-items: center;

        @media (max-width:$desktop) {
            margin-top: 24px;
        }
    }
    &__next {
        margin-left: 8px;
    }
}
