.banner-skud-two {
    @include adaptiveValue("margin-top", 120, 48, 1296, 576, 1);

    .container {
        @media (max-width:$smallTablet-max) {
            padding: 0;
        }
    }

    &__container {
        @include adaptiveValue("border-radius", 70, 42, 1296, 576, 1);
        background-color: #FBFBFB;
        padding: 64px 84px 70px;

        position: relative;
        z-index: 2;
        overflow: hidden;

        &::after {
            content: '';
            background-image: url('../img/banner.png');
            background-repeat: no-repeat;
            background-position: 129% 52%;
            background-size: 62%;
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;

            z-index: -1;

            @media (max-width:$smallTablet-max) {
                transform: rotate(49deg) translateX(-50%);
                background-position: 50% 50%;
                background-size: 100%;
                width: 800px;
                height: 550px;
                background-position: 0 0;
                left: 32%;
                top: auto;
                bottom: -122%;
            }

            @media (max-width:$mobile-max) {
                left: 26%;
                bottom: -105%;
            }

            @media (max-width:450px) {
                width: 650px;
                height: 400px;
                bottom: -88%;
            }

        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;

            background-image: url('../img/vector-2.svg');
            background-repeat: no-repeat;
            background-size: cover;
            z-index: -2;

            @media (max-width:$smallTablet-max) {
                display: none;
            }
        }

        @media (max-width:$desktop) {
            padding: 32px;
            background-color: transparent;
        }

        @media (max-width:$smallTablet-max) {
            padding: 0 0 175px 0;
        }

        @media (max-width:$mobile-max) {
            padding: 0 0 200px 0;
        }

        @media (max-width:450px) {
            padding: 0 0 150px 0;
        }
    }

    &__info {
        max-width: 578px;

        @media (max-width:$smallTablet-max) {
            max-width: none;
            text-align: center;
            max-width: calc(327px + 30px);
            margin: 0 auto;
            padding: 0 15px;
        }

    }

    &__title {
        span {
            color: var(--blue);
        }
    }

    &__descr {
        margin-top: 20px;

        font: var(--text-1);

        @media (max-width:$desktop) {
            margin-top: 12px;
        }
    }

    &__btn {
        margin-top: 59px;

        @media (max-width:$desktop) {
            margin-top: 32px;
            width: 250px;
        }
    }
}
